<template>
  <div class="Test1">
    <button class="button1" @click="lookAnswer">提交答案</button>
    <button class="button" @click="changeTest2Page">下一题</button>
    <div class="pro1">
      <div class="pro1-1">
        <input type="radio" name="answer1" value="T" :checked="choice"/><span class="word" :class="tStyle">正确</span>
        <input type="radio" name="answer1"  value="F"/><span class="word">错误</span>
      </div>
      <div class="pro1-1">
        <input type="radio" name="answer2" value="T" :checked="choice"/><span class="word" :class="tStyle">正确</span>
        <input type="radio" name="answer2"  value="F"/><span class="word">错误</span>
      </div>
      <div class="pro1-1">
        <input type="radio" name="answer3" value="T" :checked="choice"/><span class="word" :class="tStyle">正确</span>
        <input type="radio" name="answer3"  value="F"/><span class="word">错误</span>
      </div>
      <div class="countTime">
        <p>倒计时：{{ count }}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  // name: "test",
  data() {
    return {
      count: 20, // 初始值为20秒
      num:'',
      IsKeyShow: false,
      clickTime: null, //点击input框的次数，1 第一次点击；2 非第一次点击；对应前面的需求5
      choice:false,
      tStyle:'',
    };
  },
  mounted() {
    // 启动计时器，每秒更新count值
    this.timer = setInterval(() => {
      if (this.count > 0) {
        this.count--;
      } else {
        this.lookAnswer()
        clearInterval(this.timer); // 倒计时结束，停止计时器
      }
    }, 1000);
  },
  beforeDestroy() {
    // 组件销毁时清除计时器
    clearInterval(this.timer);
  },
  methods:{
    changeTest2Page(){
      this.$router.push({path:'/test21'})
    },
    lookAnswer(){
      var count=0;
      for(var i=0;i<3;i++)
      {
        var answer=document.getElementsByName("answer"+(i+1));
        if(answer[0].checked){
          count += 1;
          console.log("count="+count)
        }
      }
      alert("你一共答对了"+ count +"道题！");
      this.choice = true
      this.tStyle = "T"
    },
  }
}
</script>

